
@import '../base.scss';

.o-cart-upsell-page{
  @include t-gutters;
  transition: all 1s;
  background-color: $s-color--background-tertiary;
  max-height: t-to-rem(700);
  overflow: hidden;

  &.is-hidden{
    max-height: 0;
  }

  &__inner{
    position: relative;
    overflow: hidden;
    text-align: center;
    padding: t-to-rem(60) t-to-rem(90) t-to-rem(68);
    max-width: t-to-rem(375);
    margin: auto;
    transition: all 1s;
    
    .is-hidden &{
      padding: 0;
    }
  }
  
  &__left-image,
  &__right-image{
    position: absolute;
    inset: 0;
    width: 100%;
    object-fit: cover;
    
    &-wrapper{
      position: absolute;
      bottom: 0;
      width: t-to-rem(85);
    }
    &-container{
      padding-bottom: 120%;
      position: relative;
    }
  }

  &__left-image{
    &-wrapper{
      left: t-to-rem(-20);
    }
  }
  &__right-image{
    &-wrapper{
      right: t-to-rem(-20);
    }
  }


  &__title{
    margin-bottom: t-to-rem(32);
  }

  &__product-text{
    margin-bottom: t-to-rem(12);
    &-prefix{
      margin-bottom: t-to-rem(6);
    }
  }

  &__divider{
    width: t-to-rem(110);
    margin: t-to-rem(14) auto t-to-rem(32);
    border-top: t-to-rem(2) solid rgba($s-color--background-secondary, 0.1);

  }

  &__price-and-add-button{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__price,
  &__savings{
    &-container{
      display: flex;
    }
    &-prefix{
      margin-right: t-to-rem(4);
    }
  }

  &__add-to-cart{
    background-color: $s-color--background;
    border-radius: 100%;
    @include t-width-and-height(36);
    cursor: pointer;
    position: relative;
    svg{
      @include t-width-and-height(12);
      @include t-absolute-center-x-y;
    }
  }

  
  
  @include t-media-query($s-small-down) {

    margin: 0;

    &__inner{
      padding: t-to-rem(60) t-to-rem(90) t-to-rem(68);
      max-width: t-to-rem(375);
      margin: auto;
    }

    &__left-image,
    &__right-image{
      &-wrapper{
        width: t-to-rem(85);
      }
    }
    
    &__left-image{
      &-wrapper{
        left: t-to-rem(-20);
      }
    }
    &__right-image{
      &-wrapper{
        right: t-to-rem(-20);
      }
    }

    
    &__price-and-add-button{
      flex-direction: column;
    }
    &__add-to-cart{
      margin: t-to-rem(20) auto;
    }
  }

  @include t-media-query($s-small-up) {

    &__left-image,
    &__right-image{
      &-wrapper{
        width: t-to-rem(120);
      }
    }

    &__left-image{
      &-wrapper{
        left: 0;
      }
    }
    &__right-image{
      &-wrapper{
        right: 0;
      }
    }

    &__inner{
      max-width: t-to-rem(1250);
      margin: 0 auto;
    }
    &__main-container{
      padding: 5% 0;
    }

    
    &__product-text{
      max-width: t-to-rem(346);
      margin: 1% auto 3%;
    }

    
    &__price,
    &__savings{
      &-container{
        .has-savings &{
          flex: 1;
        }
      }
    }
    
    &__price{
      &-container{
        .has-savings &{
          justify-content: right;
        }
      }
    }
        
    &__savings{
      &-container{
        justify-content: left;
      }
    }


    
    &__add-to-cart{
      margin: 0 t-to-rem(20);
      @include t-width-and-height(42);
      transition: all $s-anim--time-short;

      &:hover{
        background-color: $s-color--background-secondary;

        svg path{
          fill: $s-color--background;
        }
      }
    }
  }

  
  @include t-media-query($s-medium-up) {

    &__left-image,
    &__right-image{
      &-wrapper{
        width: t-to-rem(160);
      }
    }
    &__add-to-cart{
      margin: 0 t-to-rem(25);
      @include t-width-and-height(48);
      svg{
        @include t-width-and-height(14);
      }
    }
  }
  
  @include t-media-query($s-xlarge-up) {

    &__left-image,
    &__right-image{
      &-wrapper{
        width: t-to-rem(200);
      }
    }
  }
}